import React from 'react';
import { Container, Wrapper, CheckboxInput, Text } from './styled';

const Checkbox = ({ label }) => {
  const [isChecked, setIsChecked] = React.useState(false);

  const handleInputClick = () => {
    setIsChecked(prev => !prev);
  };

  return (
    <Container>
      <Wrapper>
        <CheckboxInput value={isChecked} onChange={handleInputClick} />
        <Text>{label}</Text>
      </Wrapper>
    </Container>
  );
};
export default Checkbox;
