import React from 'react';
import useIsMobile from 'hooks/useIsMobile';
import MobileFooter from 'components/Footer/Mobile';
import DesktopFooter from 'components/Footer/Desktop';
import MobileMenu from 'components/MobileMenu';
import NavigationBar from 'components/NavigationBar';
import MobileNavigationBar from 'components/MobileNavigationBar';
import EntrySection from 'sections/EntrySection';
import LazypackPage from 'containers/LazypackPage';
import AttractionPage from 'containers/AttractionPage';
import { PAGE } from 'constants/index';
import GlobalStyle from './GlobalStyle';
import { ContentBlock, MenuButton } from './styled';

const App = () => {
  const [isMobileMenuShow, setIsMobileMenuShow] = React.useState(false);
  const [selectedPage, setSelectedPage] = React.useState(null);

  const isMobile = useIsMobile();

  const handleLazypackClick = () => {
    setSelectedPage(PAGE.LAZYPACK);
  };

  const handleAttractionClick = () => {
    setSelectedPage(PAGE.ATTRACTION);
  };

  const handleMenuButtonClick = () => {
    setIsMobileMenuShow(true);
  };

  const handleMenuCloseButtonClick = () => {
    setIsMobileMenuShow(false);
  };

  return (
    <>
      {/* 全域樣式 */}
      <GlobalStyle />

      {/* 頁首 */}
      {(isMobile || !selectedPage) && (
        <EntrySection
          onLazypackClick={handleLazypackClick}
          onAttractionClick={handleAttractionClick}
        />
      )}

      {/* 內容 */}
      {selectedPage && (
        <ContentBlock>
          {/* Desktop 導覽列 */}
          {!isMobile && <NavigationBar />}

          {/* Mobile 導覽列 */}
          {isMobile && <MobileNavigationBar />}

          {/* Mobile 選單 */}
          {isMobile && <MenuButton onClick={handleMenuButtonClick} />}
          {isMobile && isMobileMenuShow && (
            <MobileMenu onClose={handleMenuCloseButtonClick} />
          )}

          {selectedPage === PAGE.LAZYPACK && <LazypackPage />}

          {selectedPage === PAGE.ATTRACTION && <AttractionPage />}

          {/* 頁尾 */}
          {isMobile ? <MobileFooter /> : <DesktopFooter />}
        </ContentBlock>
      )}
    </>
  );
};

export default App;
